body {
  margin: 0;
  background: #1a1a1a;
  color: #fff;
  font-family: sans-serif;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.doorlock {
  position: relative;
  width: 400px;
  height: 500px;  /* ✅ 기존 360px 또는 400px이면 부족해요 */
  background: #333;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 0 15px rgba(0,0,0,0.5);
}

.cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;                 /* ✅ 전체 덮도록 */
  background: #555;
  transition: transform 1s ease;
  z-index: 2;
}

.cover.open {
  transform: translateY(-100%);
}

.keypad {
  position: absolute;
  top: 150px;
  width: 100%;
  text-align: center;
}

.display {
  font-size: 24px;
  background: #222;
  margin-bottom: 12px;
  padding: 8px 0;
  letter-spacing: 6px;
  border-radius: 5px;
}

.buttons {
  display: grid;
  grid-template-columns: repeat(3, 120px);  /* ✅ 각 버튼 고정 너비 */
  gap: 12px;
  margin: 0 auto;                          /* ✅ 중앙 정렬 */
  width: fit-content;                      /* ✅ 최소 너비만 사용 */
}

button {
  font-size: 20px;
  padding: 12px 0;
  background: #555;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background 0.2s;
}

button:hover {
  background: #777;
}

.success-message {
  position: absolute;
  bottom: 10px;
  width: 100%;
  text-align: center;
  font-size: 16px;
  color: #0f0;
}